<template>
    <div>
        <v-row no-gutters justify="center" class="ma-3">
            <v-col cols="12" md="7">
                <v-card outlined :loading="isLoading">
                    <v-toolbar flat>
                        <v-toolbar-title>
                             
                        </v-toolbar-title>
                        <v-spacer></v-spacer>
                        <v-fade-transition>
                            <v-btn
                                text
                                color="primary accent-4"
                                @click="resetChanges()"
                                v-show="settingChanged"
                            >
                                
                            </v-btn>
                        </v-fade-transition>
                        <v-fade-transition>
                            <v-btn
                                outlined
                                color="primary accent-4"
                                @click="saveChanges()"
                                v-show="settingChanged"
                            >
                                 
                            </v-btn>
                        </v-fade-transition>
                    </v-toolbar>

                    <v-tabs v-model="curTab">
                        <v-tab key="tab-general"></v-tab>
                        <v-tab key="tab-theme"></v-tab>
                    </v-tabs>
                    <v-tabs-items v-model="curTab" touchless>
                        <v-tab-item key="tab-general">
                            <setting-select
                                v-model="settingData"
                                :items="settingItems"
                                @change="settingChanged = true"
                            ></setting-select>
                        </v-tab-item>
                        <v-tab-item key="tab-theme">
                            <theme-manage
                                v-model="settingData.theme"
                                @change="settingChanged = true"
                            ></theme-manage>
                        </v-tab-item>
                    </v-tabs-items>
                </v-card>
            </v-col>
        </v-row>
    </div>
</template>

<script>
import axios from 'axios'
import _ from 'lodash'
import SettingSelect from '../../components/manage/SettingSelect.vue'
import ThemeManage from '../../components/manage/ThemeBuilder.vue'

export default {
    components: { SettingSelect, ThemeManage },

    data: () => ({
        curTab: 0,
        isLoading: false,
        originalSettings: {},
        settingData: {},
        settingChanged: false,
    }),
    methods: {
        async fetchSettings() {
            this.isLoading = true

            const res = await axios.get('config/admin')
            this.originalSettings = _.cloneDeep(res.data)
            this.settingData = _.cloneDeep(res.data)
            this.settingChanged = false

            this.isLoading = false
        },
        async resetChanges() {
            await this.fetchSettings()
        },
        async saveChanges() {
            this.isLoading = true

            await axios.patch('config/admin', _.cloneDeep(this.settingData))
            await this.$store.dispatch('config/fetchConfig')
            await this.fetchSettings()

            this.isLoading = false
        },
    },
    async created() {
        await this.fetchSettings()
    },
    computed: {
        settingItems() {
            return [
                {
                    type: 'text',
                    title: ' ',
                    content: '   .',
                    key: 'groupName',
                },
                {
                    type: 'line',
                },
                {
                    type: 'header',
                    title: '',
                },
                {
                    type: 'switch',
                    title: '',
                    content: '    .',
                    key: 'usePreUser',
                },
                {
                    type: 'action',
                    title: '  ',
                    content: '  .',
                    caption: '',
                    action: async () => {
                        const res = await this.$action.showConfirmDialog(
                            '  ',
                            '    ?.      .'
                        )
                        if (res) {
                            await axios.post('config/reset')
                            await this.$action.showAlertDialog(
                                '  ',
                                '   .'
                            )
                            location.reload()
                        }
                    },
                },
            ]
        },
    },
    async beforeRouteLeave(to, from, next) {
        if (this.settingChanged) {
            const res = await this.$action.showConfirmDialog(
                ' ',
                '   .  ?'
            )
            if (res) {
                next()
            } else {
                next(false)
            }
        } else {
            next()
        }
    },
}
</script>
